import React, { Component } from 'react'
import { Link } from 'react-router-dom'
import './About.css'


export default class About extends Component {
  constructor(props) {
    super(props)
    this.myRef = React.createRef()
    this.state = {
      scrollY: 0,
      active: false,
      refInView: false,
      style: {
        transform: "translateX(-50px)",
        opacity: 0,
      }
    }
  }

  componentDidMount() {
    window.addEventListener('scroll', this.handleScroll)
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScroll)
  }

  handleScroll = () => {
    // console.log(window.scrollY)
    this.setState({ scrollY: window.scrollY })
    if (this.state.scrollY > 100) {
      this.setState(prevState => ({ style: { ...prevState.style, transform: "translateX(0px)", opacity: 1 } }))
    }
    if (this.state.scrollY > 200) {
      this.setState({ active: true })
    } else if (this.state.active) {
      this.setState({ active: false })
    }
    // console.log(this.isElementInViewport(this.myRef.current));

    if (this.isElementInViewport(this.myRef.current)) {
      this.setState({ refInView: true })
    } else if (this.state.refInView) {
      this.setState({ refInView: false })
    }
  }


  scroll(ref) {
    console.log(ref)
    ref.current.scrollIntoView({ block: "center", behavior: 'smooth' })
  }

  isElementInViewport = el => {
    var rect = el.getBoundingClientRect();
    return (
      rect.top >= 0 &&
      rect.bottom <= (window.innerHeight || document.documentElement.clientHeight)
    )
  }

  render() {
    return (
      <div style={{ height: "2000px" }}>
        <button onClick={() => { this.scroll(this.myRef) }} className="footnote">Scroll</button>
        {this.state.scrollY > 50 &&
          <h3 className="text-1">This is conditionaly rendered on scroll position, will disappear on scroll up, no animation</h3>
        }
        <h3 className="text-2" style={this.state.style}>This is animated using style=this.state.style</h3>
        <h3 className={`text-3 ${this.state.active ? 'show' : ''}`}>This is animated by adding a css class and a boolean in state</h3>
        <FadeIn show={this.state.scrollY > 300 ? true : false} />
        <p ref={this.myRef} className={`target ${this.state.refInView ? 'show' : ''}`}>Scroll Here</p>
      </div >
    )
  }
}

const FadeIn = ({ show }) => {
  const componentClasses = ['text-4'];
  if (show) { componentClasses.push('show'); }
  return (
    <h3 className={componentClasses.join(' ')}>This is a component using classes like above but in a sfc</h3>
  );
};