import React, { Component } from 'react'
// import { CSSTransition } from 'react-transition-group'
import { Button } from 'reactstrap'
import { StaggeredMotion, Motion, spring } from 'react-motion'
import './Test.css'

export default class Test extends Component {
  state = {
    showBox: false,
    open: false,
    doIt: false,
    move: false,
    showMenu: false,
    block1: false,
    block2: false,
    block3: false,
  }

  toggle = () => {
    this.setState(prevState => ({ showBox: !prevState.showBox }))
  }

  handleMouseDown2 = () => {
    this.setState({ open: !this.state.open });
  }

  handleMouseDown = () => {
    this.setState({ doIt: !this.state.doIt });
  }

  handleClick = () => {
    if (this.state.showMenu) {
      this.setState(() => ({ showMenu: false }))
      setTimeout(() => { this.setState({ move: false }) }, 400)
    } else {
      this.setState({ move: true })
      setTimeout(() => { this.setState({ showMenu: true }) }, 400)
    }
  }

  clicked = (e) => {
    const blockNum = e.target.id
    this.setState({ [blockNum]: !this.state[blockNum] })
  }

  render() {
    return (
      <div>
        <Button
          size="lg"
          onMouseDown={this.handleMouseDown}>
          Toggle
        </Button>
        <div style={{ position: "relative", left: 500, width: 200, height: 200, backgroundColor: 'lightgrey' }}>
          <Motion
            defaultStyle={{ left: 0, top: 0, x: 0, y: 0, }}
            style={{ x: spring(this.state.move ? 50 : 0) }}
          >
            {(style) =>
              <div className='menu-box'
                style={{ top: `${style.x}%`, left: `${style.x}%`, transform: `translate(-${style.x}%,-${style.x}%)` }}
                onClick={this.handleClick}>
                <StaggeredMotion
                  defaultStyles={[{ x: 0 }, { x: 0 }, { x: 0 }]}
                  styles={(prevStyles) => prevStyles.map((_, i) =>
                    i === 0
                      ? { x: spring(this.state.showMenu ? -50 : 0, { stiffness: 170, damping: 15 }) }
                      : { x: spring(prevStyles[i - 1].x, { stiffness: 170, damping: 15 }) }
                  )}>
                  {(styles) =>
                    <>
                      <div style={{ position: "absolute", backgroundColor: "red", zIndex: -1, width: 20, height: 20, top: 15, left: 15, transform: `translate(0px, ${styles[0].x}px)` }} />
                      <div style={{ position: "absolute", backgroundColor: "red", zIndex: -1, width: 20, height: 20, top: 15, left: 15, transform: `translate(${styles[1].x}px, ${styles[1].x}px)` }} />
                      <div style={{ position: "absolute", backgroundColor: "red", zIndex: -1, width: 20, height: 20, top: 15, left: 15, transform: `translate(${styles[2].x}px, 0px)` }} />
                    </>
                  }
                </StaggeredMotion>
              </div>
            }
          </Motion>
        </div>
        <div style={{ position: "relative", width: "210px", height: "100px", overflow: "hidden", display: "flex" }}>
          <Motion
            defaultStyle={{ left: -30, opacity: 0, x: 0, z: 1, height: 50, width: 50 }}
            style={{
              left: spring(this.state.doIt ? 0 : -30),
              opacity: spring((this.state.doIt && !(this.state.block2 || this.state.block3)) ? 1 : 0),
              //opacity: spring((this.state.block2 || this.state.block3) ? 0 : 1),
              x: spring(this.state.block1 ? 70 : 0),
              z: this.state.block1 ? 2 : 1,
              height: spring(this.state.block1 ? 100 : 50),
              width: spring(this.state.block1 ? 100 : 50),
            }}>
            {(style) =>
              <div id="block1" className="grid-block" onClick={this.clicked} style={{
                left: style.left,
                opacity: style.opacity,
                height: style.height,
                width: style.width,
                WebkitTransform: `translate(${style.x}px, 0)`,
                transform: `translate(${style.x}px, 0)`,
                zIndex: style.z,
              }} >
                <div style={{ height: "50%", width: "50%", backgroundColor: "red" }} />
              </div>
            }
          </Motion>
          <Motion
            defaultStyle={{ top: -100, opacity: 1, x: 0, z: 1, height: 70, width: 50 }}
            style={{
              top: spring(this.state.doIt ? 0 : -100),
              opacity: spring((this.state.block1 || this.state.block3) ? 0 : 1),
              x: spring(this.state.block2 ? 70 : 0),
              z: this.state.block2 ? 2 : 1,
              height: spring(this.state.block2 ? 100 : 50),
              width: spring(this.state.block2 ? 100 : 50),
            }}>
            {(style) =>
              <div id="block2" className="grid-block" onClick={this.clicked} style={{
                top: `${style.top}%`,
                opacity: style.opacity,
                height: style.height,
                width: style.width,
                zIndex: style.z,
              }} />}
          </Motion>
          <Motion
            defaultStyle={{ right: -70, opacity: 1, x: 0, z: 1, height: 50, width: 50 }}
            style={{
              right: spring(this.state.doIt ? 0 : -70),
              opacity: spring((this.state.block1 || this.state.block2) ? 0 : 1),
              x: spring(this.state.block3 ? -70 : 0),
              z: this.state.block3 ? 2 : 1,
              height: spring(this.state.block3 ? 100 : 50),
              width: spring(this.state.block3 ? 100 : 50),
            }}>
            {(style) =>
              <div id="block3" className="grid-block" onClick={this.clicked} style={{
                right: style.right,
                opacity: style.opacity,
                height: style.height,
                width: style.width,
                WebkitTransform: `translate(${style.x}px, 0)`,
                transform: `translate(${style.x}px, 0)`,
                zIndex: style.z,
              }} />}
          </Motion>
        </div>
        <Button
          size="lg"
          onMouseDown={this.handleMouseDown2}>
          Toggle
        </Button>

        <Motion style={{ x: spring(this.state.open ? 400 : 0) }}>
          {({ x }) =>
            <div>Normal
            <div className="demo0">
                <div className="demo0-block" style={{
                  WebkitTransform: `translate(${x}px, 0)`,
                  transform: `translate(${x}px, 0)`,
                }} />
              </div>
            </div>
          }
        </Motion>
        <Motion style={{ opacity: spring(this.state.open ? 0 : 1) }}>
          {(style) =>
            <div>Opacity
            <div className="demo0">
                <div className="demo0-block" style={{
                  opacity: style.opacity,
                }} />
              </div>
            </div>
          }
        </Motion>
        <Motion style={{ x: spring(this.state.open ? 400 : 0), opacity: spring(this.state.open ? 0 : 1) }}>
          {(style) =>
            <div>Both
            <div className="demo0">
                <div className="demo0-block" style={{
                  transform: `translate(${style.x}px, 0)`,
                  opacity: style.opacity,
                }} />
              </div>
            </div>
          }
        </Motion>
        <StaggeredMotion
          defaultStyles={[{ x: 0 }, { x: 0 }, { x: 0 }]}
          styles={(prevStyles) =>
            [
              { x: spring(this.state.open ? 400 : 0) },
              { x: spring(prevStyles[0].x) },
              { x: spring(prevStyles[1].x) },
            ]}>
          {(styles) =>
            <div>Staggered
              <div className="demo0">
                <div className="demo0-block" style={{
                  transform: `translateX(${styles[0].x}px)`,
                  backgroundColor: styles[0].x > 395 && 'green'
                }} />
              </div>
              <div className="demo0">
                <div className="demo0-block" style={{
                  transform: `translateX(${styles[1].x}px)`,
                  backgroundColor: styles[1].x > 395 && 'yellow'
                }} />
              </div>
              <div className="demo0">
                <div className="demo0-block" style={{
                  transform: `translateX(${styles[2].x}px)`,
                  backgroundColor: styles[2].x > 395 && 'red'
                }} />
              </div>
            </div>
          }
        </StaggeredMotion>
        <Motion style={{ x: spring(this.state.open ? 400 : 0, { stiffness: 50, damping: 26, precision: 0.1 }) }}>
          {({ x }) =>
            <div>Slower
            <div className="demo0">
                <div className="demo0-block" style={{
                  WebkitTransform: `translate(${x}px, 0)`,
                  transform: `translate(${x}px, 0)`,
                }} />
              </div>
            </div>
          }
        </Motion>
        <Button onClick={this.toggle} size="lg">Show Box</Button>
        {/* <CSSTransition
          in={this.state.showBox}
          timeout={500}
          classNames="alert"
          unmountOnExit
        >
          <div className="box" />
        </CSSTransition> */}
      </div >
    )
  }
}

