import React from 'react';
import { Parallax, Background } from 'react-parallax'
// import layer2 from '../assets/layer-2.png'
// import layer3 from '../assets/layer-3.png'

const styles = {
  fontFamily: "sans-serif",
  textAlign: "center"
}
const insideStyles = {
  background: "white",
  padding: 20,
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%,-50%)"
}

const image1 =
  "https://images.unsplash.com/photo-1498092651296-641e88c3b057?auto=format&fit=crop&w=1778&q=60&ixid=dW5zcGxhc2guY29tOzs7Ozs%3D";
const image2 =
  "https://img00.deviantart.net/2bd0/i/2009/276/c/9/magic_forrest_wallpaper_by_goergen.jpg";
const image3 =
  "https://brightcove04pmdo-a.akamaihd.net/5104226627001/5104226627001_5297440765001_5280261645001-vs.jpg?pubId=5104226627001&videoId=5280261645001";
const image4 =
  "https://images.fineartamerica.com/images/artworkimages/mediumlarge/1/empire-state-building-black-and-white-square-format-john-farnan.jpg";

export default class News extends React.Component {
  render() {
    return (
      <div style={styles}>
        {/* <Parallax
          bgImage={layer3}
          strength={0}
          style={{ overflow: "hidden" }}
          renderLayer={percentage => {
            return (
              <div>
                <div style={{ height: 1500 }}>
                  <div
                    style={{
                      position: "absolute",
                      transform: `translateY(-${percentage * 10}%)`,
                      backgroundImage: { layer3 }
                    }}>
                    <img src={layer3} />
                  </div>
                  <div
                    style={{
                      position: "absolute",
                      top: "25%",
                      transform: `translateY(-${percentage * 40}%)`,
                    }}
                  >
                    <img src={layer2} />
                  </div>
                  <div
                    style={{
                      position: "absolute",
                      height: "100%",
                      width: "100%",
                      top: "70%",
                      transform: `translateY(-${percentage * 80}%)`,
                      // backgroundImage: `url(${layer1})`
                      backgroundImage: 'url(../assets/layer-3.png)',
                      color: "green",
                    }}>
                    <img src={layer1} />
                  </div>
                </div >
              </div >
            )
          }}
        >
        </Parallax >
        <h1>| | |</h1> */}
        <Parallax bgImage={image1} strength={1000}>
          <div style={{ height: 500 }}>
            <div style={insideStyles}>HTML inside the parallax</div>
          </div>
        </Parallax>
        <h1>| | |</h1>
        <Parallax bgImage={image3} blur={{ min: -5, max: 20 }}>
          <div style={{ height: 500 }}>
            <div style={insideStyles}>Dynamic Blur</div>
          </div>
        </Parallax>
        <h1>| | |</h1>
        <Parallax bgImage={image2} strength={-1000}>
          <div style={{ height: 500 }}>
            <div style={insideStyles}>Reverse direction</div>
          </div>
        </Parallax>
        <h1>| | |</h1>
        <Parallax strength={300}>
          <div style={{ height: 500 }}>
            <div style={insideStyles}>Use the background component for custom elements</div>
          </div>
          <Background className="custom-bg">
            <img src="http://www.fillmurray.com/500/320" alt="fill murray" />
          </Background>
        </Parallax>
        <h1>| | |</h1>
        <Parallax
          bgImage={image4}
          strength={200}
          renderLayer={percentage => (
            <div>
              <div
                style={{
                  position: "absolute",
                  background: `rgba(255, 125, 0, ${percentage * 1})`,
                  left: "50%",
                  top: "50%",
                  borderRadius: "50%",
                  transform: "translate(-50%,-50%)",
                  width: percentage * 500,
                  height: percentage * 500
                }}
              />
            </div>
          )}
        >
          <div style={{ height: 500 }}>
            <div style={insideStyles}>renderProp</div>
          </div>
        </Parallax>
        <h1>| | |</h1>

        {/* <div style={{ position: "absolute" }}>
          <img src={layer3} style={{ position: "absolute" }} />
          <img src={layer2} style={{ position: "absolute" }} />
          <img src={layer1} style={{ position: "absolute" }} />
        </div> */}
        <div style={{ height: 1000 }} />
      </div >
    )
  }
}
